<template>
  <div>
  <!---->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <!---->
  <el-card>
    <!---->
    <el-alert
      title=""
      type="info"
      center
      show-icon
      :closable="false">
    </el-alert>

    <!--  activeIndex - 0 -->
    <el-steps :active="activeIndex - 0" :space="200" finish-status="success" align-center>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>

    <el-form :model="addgoodslist" :rules="addFormRules" ref="addGoodsFormRef" label-width="100px" label-position="top">
    <el-tabs tab-position="left" v-model="activeIndex"  :before-leave="beforeLeave" @tab-click="tabClick">
      <el-tab-pane label="" nama="0">
        <el-form-item label="" prop="goods_name">
          <el-input v-model="addgoodslist.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="addgoodslist.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="addgoodslist.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="addgoodslist.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_cat">
          <el-cascader
            v-model="addgoodslist.goods_cat"
            :options="catelist"
            :props="cascaderProps"
            @change="handleChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-tab-pane>
      <el-tab-pane label="" name="1">
        <el-form-item :label="item.attr_name" v-for="item in manyTableDate" :key="item.attr_id">
          <el-checkbox-group v-model="item.attr_vals">
            <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-tab-pane>
      <el-tab-pane label="" nama="2">
        <el-form-item :label="item.attr_name" v-for="item in onlyTableDate" :key="item.attr_id">
         <el-input v-model="item.attr_vals"></el-input>
        </el-form-item>
      </el-tab-pane>
      <el-tab-pane label="" nama="3">
        <!--actionapi-->
        <el-upload
          class="upload-demo"
          action="http://127.0.0.1:8888/api/private/v1/upload"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-success="handleSuccess"
          :headers="headerObj"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
        </el-upload>
      </el-tab-pane>
      <el-tab-pane label="" nama="4">
        <!---->
        <quill-editor
          class="quillEditor"
          v-model="addgoodslist.goods_introduce"
        />

        <el-button type="primary" style="margin-top: 15px" @click="add"></el-button>
      </el-tab-pane>
    </el-tabs>
    </el-form>


  </el-card>


<!---->

    <el-dialog
      title=""
      :visible.sync="previewVisible"
      width="50%">
      <img :src="previewPath" style="width: 100%"/>
    </el-dialog>

  </div>


</template>

<script>
  import _ from 'lodash';
    export default {
        name: "addGoods",
      data(){
          return{
            parentList:[],
            //
            cascaderProps:{
              value:'cat_id',
              label:'cat_name',
              children:'children'
            },
            //ID
            selectKey:[],

            activeIndex:'0',
            catelist:[],
            addgoodslist:{
              goods_name:'',
              goods_cat:[],
              goods_price:'',
              goods_number:'',
              goods_weight:'',
              goods_introduce:'',
              pics:[],
              attrs:[],
            },
            addFormRules:{
              goods_name:[{required:true,message:'',trigger:'blur'}],
              goods_cat:[{required:true,message:'',trigger:'blur'}],
              goods_price:[{required:true,message:'',trigger:'blur'}],
              goods_number:[{required:true,message:'',trigger:'blur'}],
              goods_weight:[{required:true,message:'',trigger:'blur'}],
            },
            addDialogVisible:false,
            manyTableDate:[],
            onlyTableDate:[],

            //headers
            headerObj:{
              Authorization:window.sessionStorage.getItem('token')
            },
            //
            previewPath:'',
            previewVisible:false,
          }
      },
      created() {
       this.getCateList();
      },
      methods:{

        async getCateList(){
          const {data:res} = await this.$http.get('categories');
          if(res.meta.status !== 200){
            return this.$message.error("");
          }
          this.catelist = res.data;
          this.total = res.total;
          console.log(res.data);
          this.$message.success('');
        },

        handleChange(){
          if (this.addgoodslist.goods_cat.length !==3){
            this.addgoodslist.goods_cat = [];
          };
          console.log(this.addgoodslist.goods_cat);
        },

        beforeLeave(activeNmae,oldActiveName){
          if (oldActiveName === '0' && this.addgoodslist.goods_cat.length !== 3){
            this.$message.error('')
            return false;
          }
        },

        async tabClick(){
          //
          if (this.activeIndex === '1'){
         const {data:res} = await this.$http.get(`categories/${this.cateID-0}/attributes`, {params: {sel:'many'}});
         if(res.meta.status!==200){
           console.log(res);
           return this.$message.error('');
         }
         res.data.forEach(item=>{
          item.attr_vals = item.attr_vals.length === 0 ? [] :item.attr_vals.split(',')
         })
         this.manyTableDate = res.data;
         console.log(res.data)
          };


          //
          if(this.activeIndex==='2'){
            const {data:res} = await this.$http.get(`categories/${this.cateID-0}/attributes`, {params: {sel:'only'}});
            if(res.meta.status!==200){
              console.log(res);
              return this.$message.error('');
            }
            this.onlyTableDate = res.data;
            console.log(this.onlyTableDate);
          }

        },

        //
        handlePreview(file){
          this.previewPath = file.response.data.url;
          this.previewVisible = true;
        },
        //
        handleRemove(file){
          const filepath = file.response.data.tmp_path;
          var index;
          this.addgoodslist.pics.forEach((item,i)=> {
           if(item.tmp_path==filepath){
             index = i;
           }
          })
          this.addgoodslist.pics.splice(index,1);
          console.log(this.addgoodslist);
        },
        //
        handleSuccess(response){
          //1. 
          //2.  pushpics
          const pitInfo = {'pic':response.data.tmp_path};
          this.addgoodslist.pics.push(pitInfo);
          console.log(this.addgoodslist)
        },
        add(){
         this.$refs.addGoodsFormRef.validate(async valid=>{
           if (!valid){
             return this.$message.error("");
           }
           //
           //addgoodslist 
           //lodash cloneDeep(obj)
           var form = _.cloneDeep(this.addgoodslist);
           this.manyTableDate.forEach(item=>{
             const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals.join('')};
             form.attrs.push(newInfo);
           });
           this.onlyTableDate.forEach(item=>{
             const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals};
             form.attrs.push(newInfo);
           })

           form.goods_cat = form.goods_cat.join(',');
           console.log(form);

           const {data:res} = await this.$http.post(`goods`,form);
           if(res.meta.status !== 201){
             console.log(res)
             return this.$message.error("");
           };
           this.$message.success("");
           this.$router.push('/goods')
         })
        }

      },


      computed:{
        cateID(){
          if(this.addgoodslist.goods_cat.length === 3){
            return this.addgoodslist.goods_cat[2];
          }
          return null;
        },
      },


    }
</script>

<style scoped>
.el-checkbox{
  margin: 0 5px 0 0 !important;
}


</style>
