<template>
    <div>
      <!---->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!---->
      <el-card>
        <!---->
        <el-alert
          title=": "
          type="warning"
          show-icon
          :closable="false">
        </el-alert>

        <!---->
        <el-row class="cat_opd">
          <el-col>
            <span></span>
            <el-cascader
              v-model="selectCateKeys"
              :options="cateList"
              :props="cateProp"
              @change="handleCateChange"
              clearable
              style="width: 300px;margin-left: 20px"
            ></el-cascader>
          </el-col>
        </el-row>

        <!--tab-->
        <el-tabs v-model="activeName" @tab-click="handleClick">

          <el-tab-pane label="" name="many">
            <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
            <!---->
            <el-table :data="manyTableData"  border stripe>

              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="tagClose(i,scope.row)">
                    {{item}}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                </template>
              </el-table-column>

              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                  <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>

          <el-tab-pane label="" name="only">
            <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
            <!---->
            <el-table :data="onlyTableData"  border stripe>
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="tagClose(i,scope.row)">
                    {{item}}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                </template>
              </el-table-column>
              <el-table-column type="index" label="#"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                  <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>


      </el-card>

      <!---->
      <el-dialog :title="''+titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" >
          <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>

        </el-form>
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </el-dialog>

      <!---->
      <el-dialog :title="''+titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" >
          <el-form-item :label="titleText" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
          </el-form-item>

        </el-form>
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "params",
      data(){
          return{
           cateList:[],
            //
            selectCateKeys:[],
            cateProp:{
              value:'cat_id',
              label:'cat_name',
              children:'children'
            },

            // 
            activeName:'many',
            manyTableData:[],
            onlyTableData:[],


            //
            addDialogVisible:false,
            addForm:{
             attr_name:'',
            },
            addFormRules:{
             attr_name: [{required:true,message:'',trigger:'blur'}]
            },

            //

            editDialogVisible:false,
            editForm:{
              attr_name:'',
            },
            editFormRules:{
              attr_name: [{required:true,message:'',trigger:'blur'}]
            },

            //
            inputVisible:false,
            inputValue:"",

          }
      },

      created() {
     this.getCateList();
      },

      methods:{
          //
        async getCateList(){
          const {data:res} = await this.$http.get('categories');
          if(res.meta.status !== 200){
            return this.$message.error("");
          }
          this.cateList = res.data;
          this.total = res.total;
          console.log(res.data);
          this.$message.success('');
        },


        //IDonlymany
        async getParamsData(){

          //
          if(this.selectCateKeys.length !== 3){
            //id
            this.selectCateKeys = [];
            this.manyTableData = [];
            this.onlyTableData = [];
            return;
          };
           //
          const {data:res} = await this.$http.get(`categories/${this.cateID}/attributes`,{params:{'sel':this.activeName}});
          if (res.meta.status !== 200) {
            return this.$message.error('');
          }

          //attr_val
          res.data.forEach(item=>{
            if (item.attr_vals !== ''){
           item.attr_vals = item.attr_vals.split(',');
            }
            else if (item.attr_vals == ''){
              item.attr_vals =[];
            }
            //newtag
            item.inputVisible = false;
            item.inputValue = '';
          })

          console.log(res.data)
          if(this.activeName == 'many'){
          this.manyTableData = res.data;
          }
          else{
            this.onlyTableData = res.data;
          }
        },


        //
        handleCateChange(){
         this.getParamsData();
        },

        //tab
        handleClick(){
        this.getParamsData();
        },

        //
        addDialogClosed(){
          this.$refs.addFormRef.resetFields();
        },

        //
        addParams(){
          this.$refs.addFormRef.validate( async valid=>{
            if (!valid){
              return this.$message.error('');
            }
            const {data:res} = await this.$http.post(`categories/${this.cateID}/attributes`,{
              attr_name:this.addForm.attr_name,
              attr_sel:this.activeName,
            });
            if (res.meta.status !== 201){
              return this.$message.error('');
            };
            this.$message.success('');
            this.addDialogVisible = false;
            this.getParamsData();
          })
        },


        //
        async showEditDialog(attr_id){
          const {data:res} = await this.$http.get(`categories/${this.cateID}/attributes/${attr_id}`);
          if (res.meta.status !== 200){
            return this.$message.error('');
          };
          this.editForm = res.data
          this.editDialogVisible = true;
        },

        //
        editParams(){
          this.$refs.editFormRef.validate( async valid=>{
            if (!valid){
              return this.$message.error('');
            }
            const {data:res} = await this.$http.put(`categories/${this.cateID}/attributes/${this.editForm.attr_id}`,{
              attr_name:this.editForm.attr_name,
              attr_sel:this.activeName,
            });
            if (res.meta.status !== 200){
              return this.$message.error('');
            };
            this.$message.success('');
            this.editDialogVisible = false;
            this.getParamsData();
          })
        },

        editDialogClosed(){
          this.$refs.editFormRef.resetFields();
        },

        //ID
        removeParams(attr_id){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            const {data:res} = await this.$http.delete( `categories/${this.cateID}/attributes/${attr_id}`);
            if (res.meta.status !== 200){
              console.log(res);
              return this.$message.error('');
            }

            this.$message({
              type: 'success',
              message: '!'
            });
            this.getParamsData();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },

        //enter
        async handleInputConfirm(row){
          if (row.inputValue.trim().length === 0){
            row.inputValue = '';
            row.inputVisible = false;
            return;
          }
          //
          row.attr_vals.push(row.inputValue.trim());
          row.inputValue = '';
          row.inputVisible = false;

           const {data:res} = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`,{
            attr_name:row.attr_name,
            attr_sel:row.attr_sel,
            attr_vals:row.attr_vals.join(','),
          });

          if (res.meta.status !== 200){
            return this.$message.error('');
          };
          this.$message.success('');


        },

        showInput(row){
         row.inputVisible = true;
          this.$nextTick(()=> {
            this.$refs.saveTagInput.$refs.input.focus();
          });
        },

        async tagClose(i,row){
          row.attr_vals.splice(i,1);
          const {data:res} = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`,{
            attr_name:row.attr_name,
            attr_sel:row.attr_sel,
            attr_vals:row.attr_vals.join(','),
          });

          if (res.meta.status !== 200){
            return this.$message.error('');
          };
          this.$message.success('');

        }



      },

      computed:{
          //truefalse
        isBtnDisabled(){
          if (this.selectCateKeys.length !== 3){
            return true;
          }
          return false;
        },

        //ID
        cateID(){
          if(this.selectCateKeys.length === 3){
            return this.selectCateKeys[this.selectCateKeys.length-1];
          }
          return null;
        },
        //
        titleText(){
          if (this.activeName === 'many'){
            return '';
          }
          return ''
        },


      }
    }
</script>

<style lang="less" scoped>
.cat_opd{
  margin-top: 15px
}
  .el-tag{
    margin: 10px;
  }
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
  .input-new-tag{
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>
