<template>
    <div>
     <!---->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!---->
      <el-card>
        <!---->
        <el-row :gutter="40">
          <el-col :span="10">
            <el-input placeholder="" clearable class="input-with-select" v-model="queryinfo.query" @clear="getUsersList">
              <el-button slot="append" icon="el-icon-search" @click="getUsersList"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
         <el-button type="primary" @click="addDialogVisible = true"></el-button>
          </el-col>
        </el-row>

        <!---->
        <el-table :data="userlist" border stripe>
          <el-table-column label="#" type="index" ></el-table-column>
          <el-table-column label="" prop="username"></el-table-column>
          <el-table-column label="" prop="email"></el-table-column>
          <el-table-column label="" prop="mobile"></el-table-column>
          <el-table-column label="" prop="role_name"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#13ce66"
                inactive-color="#ff4949"
                @change="ueserStateChange(scope.row)" >
              </el-switch>

            </template>
          </el-table-column>
          <el-table-column label="" >
            <template slot-scope="scope">
              <!---->
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" circle size="mini" @click="showEditDialog(scope.row.id)"></el-button>
              </el-tooltip>
              <!---->
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="removeUserById(scope.row.id)"></el-button>
              </el-tooltip>
              <!---->
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-star-off" circle size="mini" @click="setRole(scope.row)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryinfo.pagenum"
          :page-sizes="[1,2,3,4]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

        <!---->
        <el-dialog
          title=""
          :visible.sync="addDialogVisible"
          width="30%"
        @close="addDialogClose">
          <!---->
          <el-form :model="adduserlist" :rules="addFormRules" ref="addFormRef" label-width="70px" >
            <el-form-item label="" prop="username">
              <el-input v-model="adduserlist.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="adduserlist.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="adduserlist.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mobile">
              <el-input v-model="adduserlist.mobile"></el-input>
            </el-form-item>

          </el-form>
          <span slot="footer" class="dialog-footer">
            <!---->
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addUser"> </el-button>
  </span>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
          <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" >
            <el-form-item label="">
              <el-input v-model="editForm.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="editForm.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mobile">
              <el-input v-model="editForm.mobile"></el-input>
            </el-form-item>

          </el-form>
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="setRoleDialogVisible" width="50%" @close="selectDialogClosed">
          <p>{{userInfo.username}}</p>
          <p>{{userInfo.role_name}}</p>
          <el-select v-model="selectdRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </el-dialog>

      </el-card>

    </div>
</template>

<script>
    export default {
        name: "users",
      data(){
          //
        var checkEmail = (rule,value,callback) => {
               var regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
               if (regEmail.test(value) == true){
                 return callback();
               }
               return callback(new Error(''));
        }
        // 
        var checkMobile = (rule,value,callback) => {
              var regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
          if (regMobile.test(value) == true){
            return callback();
          }
          return callback(new Error(''));
        }

          return{
            queryinfo:{
              query:'',
              //
              pagenum:1,
              //
              pagesize:2,
            },
            userlist:[],
            adduserlist:{
              username:'',
              password:'',
              email:'',
              mobile:'',
            },
            total:0,
            addDialogVisible:false,

            addFormRules:{
              username:[{required:true,message:'',trigger:'blur'},{min:3,max:10,message: '3 ~ 10',trigger: 'blur'}],
              password:[{required:true,message:'',trigger:'blur'},{min:6,max:15,message: '6 ~ 15',trigger: 'blur'}],
              email:[{required:true,message:'',trigger:'blur'},{validator: checkEmail,trigger: "blur"}],
              mobile:[{required:true,message:'',trigger:'blur'},{validator: checkMobile,trigger: "blur"}],
            },
            //
            editDialogVisible:false,
            //
            editForm:{},

            editFormRules:{
              email:[{required:true,message:'',trigger:'blur'},{validator: checkEmail,trigger: "blur"}],
              mobile:[{required:true,message:'',trigger:'blur'},{validator: checkMobile,trigger: "blur"}],},

            //
            setRoleDialogVisible:false,
            userInfo:{},
            rolesList:[],
            selectdRoleId:'',
          }
      },
      created() {
           this.getUsersList();
      },
      methods: {
        // 
        async getUsersList() {
          const {data: res} = await this.$http.get('users', {params: this.queryinfo});
          if (res.meta.status !== 200) {
            return this.$message.error('');
          }
          this.userlist = res.data.users;
          this.total = res.data.total;

        },
        //
        handleSizeChange(newSize) {
          this.queryinfo.pagesize = newSize;
          this.getUsersList();
        },
        //
        handleCurrentChange(newPage) {
          this.queryinfo.pagenum = newPage;
          this.getUsersList();
        },
        //switch
        async ueserStateChange(userinfo) {
          //`users/:uId/state/:type`
          const {data: res} = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
          if (res.meta.status !== 200) {
            userinfo.mg_state = !userinfo.mg_state;
            return this.$message.error('');
          }
          this.$message.success('');
        },
        //
        addUser() {
          this.$refs.addFormRef.validate(async valid => {
            // 
            if (!valid) {
              return
            }
            ;
            // 
            const {data: res} = await this.$http.post('users', this.adduserlist);
            if (res.meta.status == 400) {
              return this.$message.error('');
            }
            if (res.meta.status !== 201) {
              return this.$message.error('')
            }
            // 
            this.addDialogVisible = false
            this.getUsersList();
            this.$message.success('');
          })

        },
        //
        addDialogClose() {
          this.$refs.addFormRef.resetFields();

        },
        //
        async showEditDialog(id) {
          const {data: res} = await this.$http.get(`users/${id}`);
          if (res.meta.status !== 200) {
            return this.$message.error('');
          }
          this.editForm = res.data
          this.editDialogVisible = true
        },
        //
        editDialogClosed() {
          this.$refs.editFormRef.resetFields();
        },
        //
        editUserInfo() {
          this.$refs.editFormRef.validate(async (valid) => {
            if (!valid) {
              return
            }
            //
            const {data: res} = await this.$http.put(`users/${this.editForm.id}`, {
              email: this.editForm.email,
              mobile: this.editForm.mobile
            });
            if (res.meta.status !== 200) {
              return this.$message.error('');
            }
            this.editDialogVisible = false;
            this.getUsersList();
            return this.$message.success('')
          })
        },
        //Id
        removeUserById(id) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
          const {data:res} = await this.$http.delete(`users/${id}`);
          if (res.meta.status !== 200){
            console.log(res);
            return this.$message.error('');
          }

            this.$message({
              type: 'success',
              message: '!'
            });
          this.getUsersList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },
        //
        async setRole(userInfo){
          console.log('111')
          //
         const {data:res} = await this.$http.get(`roles`);
         if(res.meta.status !== 200){
           return this.$message.error('');
         }
         this.rolesList = res.data
          this.setRoleDialogVisible = true;
          this.userInfo = userInfo;
        },
        //
       async saveRoleInfo(){
          if(!this.selectdRoleId){
            return this.$message.error("");
          }
          const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{
            rid:this.selectdRoleId
          });
          if(res.meta.status!==200){
            console.log(res)
            return this.$message.error("");

          }
          this.getUsersList();
          this.setRoleDialogVisible = false;
          this.$message.success("");

        },
        //
        selectDialogClosed(){
          this.selectdRoleId = '';
        }

      }
    }
</script>

<style scoped>

</style>
