<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!---->
      <el-row :gutter="40">
        <el-col :span="10">
          <el-input placeholder="" clearable class="input-with-select" v-model="queryinfo.query" @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addGoods"></el-button>
        </el-col>
      </el-row>

      <!---->
      <el-table :data="goodslist" border stripe>
        <el-table-column label="#" type="index" ></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="" prop="goods_price"></el-table-column>
        <el-table-column label="" prop="goods_weight"></el-table-column>
        <el-table-column label="" >
          <template slot-scope="scope">
            <!---->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" circle size="mini" @click="showEditDialog(scope.row.goods_id)"></el-button>
            </el-tooltip>
            <!---->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="removeGoodsById(scope.row.goods_id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
        :page-sizes="[3,5,10]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>



      <!---->
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="editGoodsFormRef" label-width="70px" >

          <el-form-item label="">
            <el-input v-model="editForm.goods_name" prop="goods_name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_price">
            <el-input v-model="editForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_number">
            <el-input v-model="editForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_weight">
            <el-input v-model="editForm.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_state">
              <el-input disabled v-model="editForm.goods_state == 0 ?'':''"></el-input>
          </el-form-item>
          <el-form-item label="" prop="is_promote">
            <el-input disabled v-model="editForm.is_promote ? '':''"></el-input>
          </el-form-item>

        </el-form>
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoodsInfo"> </el-button>
      </el-dialog>


    </el-card>

  </div>
</template>

<script>
  export default {
    name: "list",
    data(){

      return{
        queryinfo:{
          query:'',
          //
          pagenum:1,
          //
          pagesize:3,
        },
        goodslist:[],

        total:0,



        //
        editDialogVisible:false,
        //
        editForm:{},

        editFormRules:{
          goods_name:[{required:true,message:'',trigger:'blur'}],
          goods_cat:[{required:true,message:'',trigger:'blur'}],
          goods_price:[{required:true,message:'',trigger:'blur'}],
          goods_number:[{required:true,message:'',trigger:'blur'}],
          goods_weight:[{required:true,message:'',trigger:'blur'}],
          goods_state:[{required:true,message:'',trigger:'blur'}],
          is_promote:[{required:true,message:'',trigger:'blur'}]
        },

        //
        setRoleDialogVisible:false,
        userInfo:{},
        rolesList:[],
        selectdRoleId:'',
      }
    },
    created() {
      this.getGoodsList();
    },
    methods: {
      // 
      async getGoodsList() {
        const {data: res} = await this.$http.get('goods', {params: this.queryinfo});
        if (res.meta.status !== 200) {
          return this.$message.error('');
        }
        this.goodslist = res.data.goods;
        this.total = res.data.total;

      },
      //
      handleSizeChange(newSize) {
        this.queryinfo.pagesize = newSize;
        this.getGoodsList();
      },
      //
      handleCurrentChange(newPage) {
        this.queryinfo.pagenum = newPage;
        this.getGoodsList();
      },

      //
      addGoods() {
        this.$router.push('/addGoods');

      },
      //
      addDialogClose() {
        this.$refs.addGoodsFormRef.resetFields();

      },
      //
      async showEditDialog(id) {
        const {data: res} = await this.$http.get(`goods/${id}`);
        if (res.meta.status !== 200) {
          return this.$message.error('');
        }
        this.editForm = res.data
        this.editDialogVisible = true
      },
      //
      editDialogClosed() {
        this.$refs.editGoodsFormRef.resetFields();
      },
      //
      editGoodsInfo() {
        this.$refs.editGoodsFormRef.validate(async (valid) => {
          if (!valid) {
            return
          }
          //
          const {data: res} = await this.$http.put(`goods/${this.editForm.goods_id}`, {
            goods_name: this.editForm.goods_name,
            goods_price: this.editForm.goods_price,
            goods_weight:this.editForm.goods_weight,
            goods_number:this.editForm.goods_number,
          });
          if (res.meta.status !== 200) {
            console.log(res)
            return this.$message.error('');
          }
          this.editDialogVisible = false;
          this.getGoodsList();
          return this.$message.success('')
        })
      },
      //Id
      removeGoodsById(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const {data:res} = await this.$http.delete(`goods/${id}`);
          if (res.meta.status !== 200){
            console.log(res);
            return this.$message.error('');
          }

          this.$message({
            type: 'success',
            message: '!'
          });
          this.getGoodsList();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      async setRole(userInfo){
        console.log('111')
        //
        const {data:res} = await this.$http.get(`roles`);
        if(res.meta.status !== 200){
          return this.$message.error('');
        }
        this.rolesList = res.data
        this.setRoleDialogVisible = true;
        this.userInfo = userInfo;
      },
      //
      async saveRoleInfo(){
        if(!this.selectdRoleId){
          return this.$message.error("");
        }
        const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{
          rid:this.selectdRoleId
        });
        if(res.meta.status!==200){
          console.log(res)
          return this.$message.error("");

        }
        this.getGoodsList();
        this.setRoleDialogVisible = false;
        this.$message.success("");

      },
      //
      selectDialogClosed(){
        this.selectdRoleId = '';
      }

    }
  }
</script>

<style scoped>

</style>
