<template>
<div>
  <!---->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <!---->
  <el-card>
    <!---->
    <el-row>
      <el-col>
        <el-button type="primary" @click="showCateDialog"></el-button>
      </el-col>
    </el-row>

    <!---->
    <tree-table class="tree_table" :data="categories" :columns="columns" :expand-type="false" :show-index="true" index-text="#" border :selection-type="false">
      <!---->
      <template slot="isok" slot-scope="scope">
        <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
        <i class="el-icon-error" v-else style="color:#ff0000"></i>
      </template>
      <!---->
      <template slot="order" slot-scope="scope">
        <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
        <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
        <el-tag type="warning" size="mini" v-else></el-tag>
      </template>
      <!---->
      <template slot="opt" slot-scope="scope">
        <el-button type="primary" class="el-icon-edit" size="mini" @click="showEditDialog(scope.row.cat_id)"></el-button>
        <el-button type="danger" class="el-icon-delete" size="mini" @click="deleteCate(scope.row.cat_id)"></el-button>
      </template>
    </tree-table>
    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="querInfo.pagenum"
      :page-sizes="[3,5,6]"
      :page-size="querInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>

  <!---->

  <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">

    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="70px" >
      <el-form-item label="">
        <el-input v-model="addCateForm.cat_name" prop="cat_name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-cascader
          v-model="selectKey"
          :options="parentList"
          :props="cascaderProps"
          @change="ParentCateChange"
          clearable
          change-on-select
        ></el-cascader>
      </el-form-item>


    </el-form>


    <el-button @click="addCateDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addCate"> </el-button>
  </el-dialog>


  <!---->
  <el-dialog title="" :visible.sync="editCateDialogVisible" width="50%" @close="editCateDialogClosed">
    <el-form :model="editCateForm" :rules="editCateFormRules" ref="editCateFormRef" label-width="70px" >
      <el-form-item label="">
        <el-input v-model="editCateForm.cat_name" prop="cat_name"></el-input>
      </el-form-item>

    </el-form>
    <el-button @click="editCateDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editCate(editCateForm.cat_id)"> </el-button>
  </el-dialog>

</div>
</template>

<script>
    export default {
        name: "cate",
      data(){
         return{
           //
           categories:[],
           querInfo:{
             type:3,
             pagenum:1,
             pagesize:5,
           },
           //
           total:0,
           //tabletree
           columns:[{
             label:'',
             prop:'cat_name',
           },
             {
               label: "",
               //.
               type:'template',
               //
               template:'isok'
             },
             {
               label: "",
               //.
               type:'template',
               //
               template:'order'
             },
             {
               label: "",
               //.
               type:'template',
               //
               template:'opt'
             }],



           //
           addCateDialogVisible:false,
           addCateForm:{
             cat_name:'',
             //ID
             cat_pid:'',
             //
             cat_level:0,
           },
           parentList:[],
           //
           cascaderProps:{
             value:'cat_id',
             label:'cat_name',
             children:'children'
           },
           //ID
           selectKey:[],
           addCateFormRules:{cat_name:{required:true,message:'',trigger:'blur'},},



           //
           editCateDialogVisible:false,
           editCateForm:{
             cat_name:'',
             cat_id:'',
           },
           editCateFormRules:{
             cat_name:{required:true,message:'',trigger:'blur'},
           }
         }
      },
      created() {
          this.getCateList();
      },
      methods:{
         async getCateList(){
         const {data:res} = await this.$http.get('categories',{params:this.querInfo});
         if(res.meta.status !== 200){
           return this.$message.error("");
         }
         this.categories = res.data.result;
         this.total = res.total;
         console.log(res.data);
         this.$message.success('');
         },
        handleSizeChange(newSize){
           this.querInfo.pagesize = newSize;
           this.getCateList();
        },
        handleCurrentChange(newPage){
        this.querInfo.pagenum = newPage;
        this.getCateList();
        },
        // 
        showCateDialog(){
           this.getParentCateList();
           this.addCateDialogVisible = true;
        },
        //
       async getParentCateList(){
           const {data:res} = await this.$http.get('categories',{params:{type:2}});
         if(res.meta.status !== 200){
           return this.$message.error("");
         }
         this.parentList = res.data;
        },
        ParentCateChange(){
           //selectKey012
           console.log(this.selectKey)
          if (this.selectKey.length>0) {
            this.addCateForm.cat_pid = this.selectKey[this.selectKey.length - 1]; //ID
            this.addCateForm.cat_level = this.selectKey.length;//
          }else{
            this.addCateForm.cat_pid = 0; //ID
            this.addCateForm.cat_level = 0;//
          }
        },
       async addCate(){
         this.$refs.addCateFormRef.validate(async valid => {
           // 
           if (!valid) {
             return
           }
           ;
           // 
           const {data: res} = await this.$http.post('categories', this.addCateForm);
           if (res.meta.status == 400) {
             return this.$message.error('');
           }
           if (res.meta.status !== 201) {
             return this.$message.error('')
           }
           // 
           this.addCateDialogVisible = false
           this.getCateList();
           this.$message.success('')
         })
        },
        addCateDialogClosed(){
          this.selectKey = [];
          this.addCateForm.cat_level = 0;
          this.addCateForm.cat_pid = 0;
          this.addCateForm.cat_name = ''
        },

        showEditDialog(id){

           this.editCateDialogVisible = true;
           this.editCateForm.cat_id = id;
           },

        async editCate(id){
          this.$refs.editCateFormRef.validate(async valid => {
              // 
              if (!valid) {
                return
              };
              // 
           const {data:res} = await this.$http.put(`categories/${id}`,{cat_name:this.editCateForm.cat_name});
          if(res.meta.status !== 200){
            console.log(res)
            return this.$message.error("");
          };
            this.getCateList();
            this.editCateDialogVisible = false;
          this.$message.success("");
        })},

        editCateDialogClosed(){
           this.$refs.editCateFormRef.resetFields();
        },


        deleteCate(id){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            const {data:res} = await this.$http.delete(`categories/${id}`);
            if (res.meta.status !== 200){
              console.log(res);
              return this.$message.error('');
            }

            this.$message({
              type: 'success',
              message: '!'
            });
            this.getCateList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }


      },

    }
</script>

<style lang="less" scoped>
.tree_table{
  margin-top: 15px;
}

</style>
