<template>
<div class="login_container">
  <div class="login_box">
    <!--  -->
    <div class="avatat_box">
      <img src="../assets/logo.png">
    </div>

    <!---->
    <el-form :model="loginform" :rules="rules" ref="loginFormRef" label-width="0px" class="login_form" >
      <el-form-item prop="username">
        <el-input v-model="loginform.username" prefix-icon="el-icon-user-solid"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input v-model="loginform.password" prefix-icon="el-icon-scissors" type="password"></el-input>
      </el-form-item>
      <el-form-item class="btns">
        <el-button type="primary" @click="login"></el-button>
        <el-button type="info" @click="resetloginForm"></el-button>
      </el-form-item>
    </el-form>

  </div>
</div>
</template>

<script>
    export default {
        name: "login",
      data(){
          return {
            //
            loginform:{
              username:'admin',
              password:'123456',
            },
            //
            rules:{
              username:[{required:true,message:'',trigger:'blur'},{min:3,max:10,message: '',trigger: 'blur'}],
              password:[{required:true,message:'',trigger:'blur'},{min:3,max:10,message: '',trigger: 'blur'}]
            }
          }
      },
      methods:{
          //
          resetloginForm(){
            this.$refs.loginFormRef.resetFields();
            this.loginform = {
              username:'',
              password:'',}
          },
        // 
        login(){
            this.$refs.loginFormRef.validate(async (valid)=>{

              if(!valid){
              return;// 
            }else{
               const {data:res} = await this.$http.post('login',this.loginform);
                console.log(res)
              if(res.meta.status !== 200){
                 return this.$message.error('');
              }else {
                 this.$message.success('');
                 //tokenSessionStorage
                //apitoken
                //SessionStorage 
                 window.sessionStorage.setItem('token',res.data.token);
                 // /home
                 this.$router.push('/home');
              }

              }

            })
        }
      }
    }
</script>

<style lang="less" scoped>
.login_container{
  height: 100%;
  background: #2b4b6b;
}
  .login_box{
    width: 450px;
    height:300px;
    background: white;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    .avatat_box{
      width: 130px;
      height: 130px;
      border-radius: 50%;
      background: white;
      border: 1px solid #eee;
      box-shadow: 0 0 10px #ddd;
      padding: 10px;
      position: absolute;
      left: 50%;
      transform: translate(-50%,-50%);
      img{
        border-radius: 50%;
        background: #eee;
        width: 100%;
        height:100%;
      }
    }
  }

  .login_form{
    position: absolute;
    bottom: 0px;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .btns{
    display: flex;
    justify-content: flex-end;
  }

</style>
