<template>
    <div>
      <!---->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!---->
      <el-card>
        <el-row>
          <!---->
          <el-col>
            <el-button type="primary" @click="addDialogVisible = true"></el-button>
          </el-col>
        </el-row>

        <!---->
     <el-table :data="roleList" border stripe>
       <!---->
       <el-table-column type="expand">
         <template slot-scope="scope">
           <el-row :class="['bdbottom',i1 ===0 ? 'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
             <!---->
             <el-col :span="5"><el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag><i class="el-icon-caret-right"></i></el-col>
             <!---->
             <el-col :span="19">
               <el-row :class="['vcenter',i2 !==0 ? 'bdtop':'']" v-for="(item2,i2) in item1.children" :key="item2.id">
                 <!--for-->
                 <el-col :span="6">
                   <el-tag closable type="success" @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                   <i class="el-icon-caret-right"></i>
                 </el-col>
                 <el-col :span="18">
                   <el-tag closable type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                 </el-col>
               </el-row>
             </el-col>
           </el-row>
         </template>
       </el-table-column>
       <!---->
       <el-table-column type="index" label="#"></el-table-column>
       <el-table-column label="" prop="roleName"></el-table-column>
       <el-table-column label="" prop="roleDesc"></el-table-column>
       <el-table-column label="" width="300px">
         <template slot-scope="scope">
           <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
           <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteRoleById(scope.row.id)"></el-button>
           <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
         </template>
       </el-table-column>
     </el-table>

      </el-card>




      <!---->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="30%"
        @close="addRoleDialogClosed">
        <!---->
        <el-form :model="addRoles" :rules="addFormRules" ref="addRoleFormRef" label-width="80px" >
          <el-form-item label="" prop="roleName">
            <el-input v-model="addRoles.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addRoles.roleDesc"></el-input>
          </el-form-item>

        </el-form>
        <span slot="footer" class="dialog-footer">
            <!---->
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addRole"> </el-button>
  </span>
      </el-dialog>




      <!---->
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
        <el-form :model="editForm" :rules="editFormRules" ref="roleFormRef" label-width="70px" >
          <el-form-item label="">
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.roleDesc"></el-input>
          </el-form-item>

        </el-form>
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </el-dialog>


      <!---->
      <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">

        <el-tree show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" :data="rightsList" :props="treeProps" ref="treeRef"></el-tree>

        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click='allotRights'> </el-button>
      </el-dialog>









    </div>
</template>

<script>
    export default {
        name: "roles",
      data(){

          return{
            //
            roleList:[],

            //
            editForm:{},
            editFormRules:{
              roleName:[{required:true,message:'',trigger:'blur'}],
              roleDesc:[{required:true,message:'',trigger:'blur'}],},
            editDialogVisible:false,

            //
            addRoles:{
              roleName:'',
              roleDesc:''
            },
            addDialogVisible:false,
            addFormRules:{
              roleName:[{required:true,message:'',trigger:'blur'}],
              roleDesc:[{required:true,message:'',trigger:'blur'},{min:6,max:15,message: '5 ~ 15',trigger: 'blur'}],
            },

            //
            setRightDialogVisible:false,
            rightsList:[],
            //
            treeProps:{
              label:'authName',
              children:'children'
            },
            //id
            defKeys:[],
            //ID
            roleId:'',
          }
          },

      created() {
          this.getRolesList();
      },
      methods:{
         async getRolesList(){
            const {data:res} = await this.$http.get(`roles`);
            if (res.meta.status !== 200){
              return this.$message.error('');
            }
            this.roleList = res.data;
            console.log(res.data);
          },
        //ID,
        removeRightById(scopeRow,id2){
           var role = scopeRow;
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            const {data:res} = await this.$http.delete(`roles/${role.id}/rights/${id2}`);
            if (res.meta.status !== 200){
              console.log(res);
              return this.$message.error('');
            }

            this.$message({
              type: 'success',
              message: '!'
            });

            // this.getRolesList(); 
            role.children = res.data;
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },

        //
        editRoleInfo() {
          this.$refs.roleFormRef.validate(async (valid) => {
            if (!valid) {
              return
            }
            //
            const {data: res} = await this.$http.put(`roles/${this.editForm.roleId}`, {
              roleName: this.editForm.roleName,
              roleDesc: this.editForm.roleDesc
            });
            if (res.meta.status !== 200) {
              return this.$message.error('');
            }
            this.editDialogVisible = false;
            this.getRolesList();
            return this.$message.success('')
          })
        },

        //
        editDialogClosed() {
          this.$refs.roleFormRef.resetFields();
        },

        //
        async showEditDialog(id) {
          const {data: res} = await this.$http.get(`roles/${id}`);
          if (res.meta.status !== 200) {
            return this.$message.error('');
          }
          this.editForm = res.data;
          this.editDialogVisible = true;
          console.log(this.editForm);
        },
        //ID
        deleteRoleById(id){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {
            const {data:res} = await this.$http.delete(`roles/${id}`);
            if (res.meta.status !== 200){
              console.log(res);
              return this.$message.error('');
            }

            this.$message({
              type: 'success',
              message: '!'
            });
            this.getRolesList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        },

        //
        addRole(){
          this.$refs.addFormRef.validate(async valid => {
            // 
            if (!valid) {
              return
            }
            ;
            // 
            const {data: res} = await this.$http.post('roles', this.addRoles);
            if (res.meta.status == 400) {
              return this.$message.error('');
            }
            if (res.meta.status !== 201) {
              return this.$message.error('')
            }
            // 
            this.addDialogVisible = false
            this.getRolesList();
            this.$message.success('')
          })
        },

        //
        addRoleDialogClosed() {
          this.$refs.addRoleFormRef.resetFields();

        },

        //
       async showSetRightDialog(role){
           //id
           this.roleId = role.id;
           const {data:res} = await this.$http.get('rights/tree')
         if(res.meta.status !== 200){
           return this.$message.error('');
         }
         this.rightsList = res.data
         this.getLeafKeys(role,this.defKeys);
         this.setRightDialogVisible = true;
         console.log(this.rightsList)
        },
        //id
        getLeafKeys(node,arr){
           //
           if(!node.children){
             return arr.push(node.id);
           }
           node.children.forEach((item)=>{
              this.getLeafKeys(item,arr);
           })
        },
        //
        setRightDialogClosed(){
           this.defKeys = [];
        },
        //
       async allotRights(){
           const keys = [
             ...this.$refs.treeRef.getCheckedKeys(),
             ...this.$refs.treeRef.getHalfCheckedKeys()
           ];
           const idStr = keys.join(',');
          const {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr});
          if(res.meta.status !== 200){
            return this.$message.error('');
          };
          this.getRolesList();
          this.$message.success('');

        }

      }

    }
</script>

<style scoped>
.el-tag{
  margin: 10px;
}
  .bdtop{
    border-top: 1px solid #eee;
  }
  .bdbottom{
    border-bottom: 1px solid #eee;
  }
  .vcenter{
    display: flex;
    align-items: center;
  }
</style>
